<template>
  <view class="detail-service">
    <view class="v-address" @click="toMap">
      <view class="icon">
        <view class="icon-map">
          <image
            class="img"
            src="https://ps.hubeisports.cn/static/hubei/images/icon-map.png"
            alt=""
          />
        </view>
        <!-- <text class="text">导航</text> -->
      </view>
      <view class="main">{{ info.detail_address ? info.detail_address : '暂无地址' }}</view>
    </view>
    <view class="v-phone" @click="toPhone">
      <view class="icon">
        <image
          class="img"
          src="https://ps.hubeisports.cn/static/hubei/images/icon-call.png"
          alt=""
        />
      </view>
      <text class="text">电话</text>
    </view>
  </view>
</template>

<script>
export default {
  name: 'detailService',
  props: {
    info: {
      type: Object,
      default: () => {
        return {}
      },
    },
  },
  data() {
    return {}
  },
  methods: {
    toPhone() {
      let phone = this.info.contact_phone || this.info.service_tel
      if (!phone) {
        this.$toast('暂无联系方式')
        return
      }
      uni.makePhoneCall({
        phoneNumber: phone,
        fail(err) {
          console.error(err)
        },
      })
    },
    toMap() {
      if (!this.info.lat || !this.info.lng) {
        this.$toast('暂无位置信息')
        return
      }
      uni.openLocation({
        latitude: Number(this.info.lat), //要去的纬度
        longitude: Number(this.info.lng), //要去的经度
        address: this.info.detail_address ?? '', //要去的具体地址
        scale: 16,
      })
    },
  },
}
</script>

